<template>
  <div class="allPage">
    <div class="core_container">
      <div class="form_container">
        <el-form
          :inline="true"
          :model="requestParam"
          class="demo-form-inline form_container_header"
          size="mini"
        >
          <el-form-item label="AppId" :required="true">
            <el-input v-model.number="requestParam.app_id"></el-input>
          </el-form-item>
          <el-form-item label="BossId">
            <el-input v-model.number="requestParam.boss_id"></el-input>
          </el-form-item>
          <el-form-item label="渠道码">
            <el-input v-model="requestParam.channel_code"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getList">查询</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="showDataTab">
        <template>
          <el-button type="success" size="mini" @click="add">新增</el-button>
          <el-button type="warning" size="mini" @click="update">修改</el-button>
          <el-button type="danger" size="mini" @click="remove">删除</el-button>
        </template>
        <el-table
          :data="page.list"
          style="width: 100%;margin-top:5px;"
          :row-class-name="tableRowClassName"
        >
          <el-table-column prop="key" label="Key"> </el-table-column>
          <el-table-column prop="secert" label="Secert"> </el-table-column>
        </el-table>
        <template>
          <el-dialog
            :title="action.title"
            :visible.sync="dialogFormVisible"
            show-close:true
          >
            <el-form :model="secret" ref="secret">
              <el-form-item
                label="AppId"
                :label-width="formLabelWidth"
                prop="app_id"
                :required="true"
              >
                <el-input
                  v-model.number="secret.app_id"
                  class="dialog_input"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="BossId"
                :label-width="formLabelWidth"
                prop="boss_id"
                :required="true"
              >
                <el-input
                  v-model.number="secret.boss_id"
                  class="dialog_input"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="渠道码"
                :label-width="formLabelWidth"
                prop="channel_code"
              >
                <el-input
                  v-model="secret.channel_code"
                  class="dialog_input"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="FaceBook ID"
                :label-width="formLabelWidth"
                prop="fb_app_id"
                :required="true"
              >
                <el-input
                  v-model="secret.fb_app_id"
                  class="dialog_input"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="FaceBook Secret"
                :label-width="formLabelWidth"
                prop="secret"
                :required="true"
                v-show="action.type!='remove'"
              >
                <el-input
                  v-model="secret.fb_secret"
                  class="dialog_input"
                ></el-input>
              </el-form-item>
              <el-form-item>
                <el-button
                  @click="(dialogFormVisible = false), resetForm()"
                  style="margin-left:20%;"
                  >取 消</el-button
                >
                <el-button type="primary" @click="doCommit">确 定</el-button>
              </el-form-item>
            </el-form>
          </el-dialog>
        </template>
      </div>
      <div class="pageBar">
        <el-pagination
          background
          hide-on-single-page
          layout="prev, pager, next"
          @next-click="nextPage"
          @current-change="thisPage"
          @prev-click="prePage"
          :current-page="page.current_page"
          :page-size="page.page_size"
          :total="page.total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      requestParam: {
        app_id: "",
        boss_id: "",
        channel_code: ""
      },
      responseData: [],
      page: {
        list: [],
        current_page: 1,
        total: 0,
        page_size: 8
      },
      showTab: false,
      action: {
        type: "",
        title: "新增配置"
      },
      secret: {
        boss_id: "", //y number
        app_id: "", //y number
        channel_code: "", //y number
        fb_app_id: "",
        fb_secret: ""
      },
      dialogFormVisible: false,
      formLabelWidth: "20%"
    };
  },
  methods: {
    getList() {
      var that = this;
      that.responseData = [];
      this.$api.app.fbSecretList(this.requestParam).then(res => {
        if (res.data.success === true) {
          that.responseData = res.data.data.list;
          that.page.total = that.responseData.length;
          const start = (that.page.current_page - 1) * that.page.page_size;
          const end =
            that.page.total > that.page.current_page * that.page.page_size
              ? that.page.current_page * that.page.page_size
              : that.responseData.length;
          that.page.list = that.responseData.slice(start, end);
        } else {
          this.$message({
            showClose: true,
            message: "获取FaceBook密匙列表失败",
            type: "error"
          });
        }
      });
    },
    resetForm() {
      this.secret = {
        boss_id: "",
        app_id: "",
        channel_code: "",
        fb_app_id: "",
        fb_secret: ""
      };
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 4 === 1) {
        return "warning-row";
      } else if (rowIndex % 4 === 3) {
        return "success-row";
      }
      return "";
    },
    doCommit() {
      let that = this;
      if (this.action.type == "add" || this.action.type == "update") {
        this.$api.app.updateFbSecret(this.secret).then(res => {
          let successMsg =
            that.action.type == "add" ? "新增成功!" : "修改成功!";
          let falureMsg = that.action.type == "add" ? "新增失败!" : "修改失败!";
          if (res.data.success === true) {
            this.$message({
              showClose: true,
              message: successMsg,
              type: "success"
            });
            that.dialogFormVisible = false;
            that.resetForm();
            that.methods.getList();
          } else {
            this.$message({
              showClose: true,
              message: falureMsg,
              type: "error"
            });
          }
        });
      } else if (this.action.type == "remove") {
        this.$api.app.deleteFbSecret(this.secret).then(res => {
          if (res.data.success === true) {
            this.$message({
              showClose: true,
              message: "移除成功！",
              type: "success"
            });
            that.dialogFormVisible = false;
            that.resetForm();
            that.methods.getList();
          } else {
            this.$message({
              showClose: true,
              message: "移除失败",
              type: "error"
            });
          }
        });
      }
    },
    add() {
      this.dialogFormVisible = true;
      this.action.type = "add";
      this.action.title = "新增配置";
      this.resetForm();
    },
    update() {
      this.dialogFormVisible = true;
      this.action.type = "update";
      this.action.title = "修改";
      this.resetForm();
    },
    remove() {
      this.dialogFormVisible = true;
      this.action.type = "remove";
      this.action.title = "删除";
      this.resetForm();
    },

    prePage() {
      if (this.page.current_page > 0) {
        this.page.current_page--;
        this.page.list = this.responseData.slice(
          (this.page.current_page - 1) * this.page.page_size,
          this.responseData.length >
            this.page.current_page * this.page.page_size
            ? this.page.current_page * this.page.page_size
            : this.responseData.length
        );
      }
    },
    thisPage(cpage) {
      this.page.current_page = cpage;
      this.page.list = this.responseData.slice(
        (this.page.current_page - 1) * this.page.page_size,
        this.responseData.length > this.page.current_page * this.page.page_size
          ? this.page.current_page * this.page.page_size
          : this.responseData.length
      );
    },
    nextPage() {
      if (
        this.page.current_page <
        Math.ceil(this.page.total / this.page.page_size)
      ) {
        this.page.current_page++;
        this.page.list = this.responseData.slice(
          (this.page.current_page - 1) * this.page.page_size,
          this.responseData.length >
            this.page.current_page * this.page.page_size
            ? this.page.current_page * this.page.page_size
            : this.responseData.length
        );
      }
    }
  }
};
</script>

<style lang="less" scoped>
.dialog_input {
  width: 70%;
}
.dialog_input_small {
  width: 20%;
  margin: 0 20px;
}
</style>



